import React, { useState } from 'react';
import Airtable from 'airtable';

const base = new Airtable({ apiKey: process.env.REACT_APP_AIRTABLE_API_KEY }).base(process.env.REACT_APP_AIRTABLE_BASE_ID);

const formStyle = {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '50px',
    backgroundColor: '#f2f2f2',
    borderRadius: '5px',
    boxShadow: '0px 0px 15px 5px rgba(0,0,0,0.06)',
    width: '70%',
    margin: '0 auto',
};

const inputStyle = {
    margin: '20px 0',
    padding: '15px',
    width: '100%',
    border: '1px solid #ccc',
    borderRadius: '4px',
    boxSizing: 'border-box',
    fontSize: '16px',
};

const buttonStyle = {
    padding: '10px 20px',
    backgroundColor: '#4CAF50',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    fontSize: '16px',
    marginTop: '20px',
};

function ContactUs() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");

    const handleSubmit = (e) => {
        e.preventDefault();

        base('tblr22vQyiP2qrZUv').create([
            {
                "fields": {
                    "Name": name,
                    "Email": email,
                    "Message": message
                }
            }
        ], function(err, records) {
            if (err) {
                console.error(err);
                return;
            }
            records.forEach(function (record) {
                console.log(record.getId());
            });
        });

        setName("");
        setEmail("");
        setMessage("");
    };

    return (
        <form style={formStyle} onSubmit={handleSubmit}>
            <input style={inputStyle} type="text" name="name" placeholder="Your name" value={name} onChange={e => setName(e.target.value)} required />
            <input style={inputStyle} type="email" name="email" placeholder="Your email" value={email} onChange={e => setEmail(e.target.value)} required />
            <textarea style={inputStyle} name="message" placeholder="Your message" value={message} onChange={e => setMessage(e.target.value)} required />
            <input style={buttonStyle} type="submit" value="Submit" />
        </form>
    );
}

export default ContactUs;


